.container-fluid {
    padding:0
}

.row {
    margin: 0;
}

a:hover {
    text-decoration: none;
}

.appeal-listing {
    box-sizing: border-box;
    font-size: 0.8rem;
}

.listing-header {
    font-size:1rem;
    font-weight: bold;

    .text-dark:hover{
        text-decoration: underline;
    }
}


.sticky-div {
      position: -webkit-sticky;
      position: sticky;
      top: 0;
        bottom: 0;
        height: 100vh;
        overflow-y: auto;
}

.conversationsList {
    min-height: calc(100vh - 56px)
}

.newMessageForm {
    background-color: white;
    position: absolute;
    bottom: 0;
    height: 100px;
}

.messages {
    height: calc(100vh - 56px - 100px);
    overflow-y: scroll;
}

.chat-left {
    border-right: 1px solid lightgray
}

.message-input {
    border-radius: 5px;
    padding: 10px 20px;
    border: 1px solid grey
}

.send-btn {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    width: 100%;
    text-align: center;
}

.convo-item{
    transition-duration: 0.2s;
    border-bottom: 1px solid lightgrey;
}

.convo-item:hover{
    background-color: lightgray;
    cursor: pointer;
}

.options-div {
    padding: 5%
}
.sent-message .text-body {
	color: white
}

.received-message .text-body {
    background-color: lightgray;
}

.text-bod {
    display: inline-block;
    padding: 10px 25px;
    border-radius: 10px;
    color: white;
}

.font-sm {
    font-size: 0.8rem;
}


.dashboard-path a {
    font-size: 1.4rem;
    padding: 10px;
    color: white;
    transition-duration: 0.2s;
}

.dashboard-path a:hover{
    color: darkred;
    text-decoration: none;
}

.dashboard-path .active{
    border-bottom: 1px solid white
}

.sort-select {
    display: inline-block;
    border: 1px solid lightgray;
    border-radius: 10px;
    padding: 3px 10px;
    width: 250px;
}

.get-started {
      width: 100%;
      height: 100%;
      overflow-y: hidden;
}

.center-element {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
}


.result-thumbnail {
    padding: 0;
}

.result-thumbnail img {
    width: 100%;
    object-fit: cover;
    cursor: pointer
}

.w-90{
    width: 90%
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.convo-item-pic img {
    border-radius: 100%;
    width: 50px;
    height: 50px;
}


/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 4px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.share-link {
    border: 1px solid lightgray;
    padding: 3px 10px;
    border-radius: 3px;
}
